<template>
  <div id="me">
    <head-top title="我的设备"></head-top>
      <ul class="deviceList">
      <li class="list_li" @click='listtiao()'
        v-for=" item in deviceList"
        :key="item.id"
      >
        <div
          class="left"
          ref="sideLeft"
        >
          <img
            src="../../assets/device/startai_on@3x.png"
            alt=""
            v-if="false"
          >
          <img
            src="../../assets/device/startai_off@3x.png"
            alt=""
            v-if="true"
          >
          <div class="deviceName">
            <span>{{item.name}}</span>
            <span v-if="item.state">设备在线</span>
            <span
              class="deviceOFF"
              v-if="!item.state"
            >已关闭</span>
          </div>
          <!-- <span class="currTemp">当前温度:<font style="color:#3551c1"> {{item.currTemp}}℃</font></span> -->
          <span class="currTemp">运行正常</span>
        </div>
       
      </li>
    </ul>
    <!-- <div
      class="adDevice"
      @click="add"
    >
      <img
        src="../../assets/device/add@3x.png"
        alt=""
      >
      <span>添加设备</span>
    </div> -->
    <!-- 删除设备 -->
    <messageBox
      setHeight="150"
      setType="confirm"
      v-show="messageBoxType === 'Deletedevice'"
    >
      <div
        slot="confirmColumn"
        class="common_alert"
      >
        <div class="alert_icon">
          <span>删除设备</span>
        </div>
        <div class="alert_describe">删除后需要重新扫描绑定，删除吗？</div>
        <div class="alert_operation">
          <div
            class="determine_one"
            @click="alertChoose(0)"
          >取消</div>
          <div
            class="determine_one ok"
            @click="alertChoose(1)"
          >确定</div>
        </div>
      </div>
    </messageBox>
    <transition
      mode="in-out"
      name="slide-left"
    >
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>

import messageBox from '../../components/common/messageBox'
import { mapActions, mapGetters } from 'vuex'
import headTop from '../../components/common/head'
import {
  temperatureAndHumidityDataRequest,
  callThePhoneScanRequest
} from '../../api/method'
import {
  switchNetworkResponse,
  temperatureAndHumidityDataResponse,
  deviceListResponse,
  callThePhoneScanResponse
} from '../../api/getData'
import { getStore } from '../../utils/index'
export default {
  data () {
    return {
      flagdelter: true, // 左滑
      startX: null, // 水平移动距离
      startY: null, // 垂直移动距离
      delterflag: false, // true 左滑按钮显示
      endX: null,
      active: 'me',
      currTemperature: 16,
      // deviceList: {
      //   name: '供暖插座',
      //   state: true,
      //   currTemp: 23,
      //   mac: ''
      // },
      deviceList: [{
        name: '振动光纤',
        state: true,
        currTemp: 23,
        mac: ''
      }],
      disJump: true
    }
  },
  components: {
    'head-top': headTop,
    messageBox
  },
  computed: {
    ...mapGetters(['messageType', 'currentRouter', 'devicesData', 'firstdevicePage']),
    messageBoxType () {
      return this.messageType
    },

    devicesListformat () {
      let obj = this.devicesData
      console.log(this.devicesData)
      return obj
    }
  },
  created () {
    
      console.log('create'+this.$root.ISDEBUG)
    temperatureAndHumidityDataRequest(this.NAVIGATOR, this)
  },
  mounted () {
    // console.log(this.temperature, 'dddddd')
    if (this.firstdevicePage) {
      // this.$router.push({path: '/Home/device/temperature'}) 
      this.$store.state.user.temperature = false
    }
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'device')
     switchNetworkResponse(this.NAVIGATOR, this)
    callThePhoneScanResponse(this.NAVIGATOR, this)
    deviceListResponse(this.NAVIGATOR, this)
    if (this.$root.ISDEBUG) {
      // window.deviceListResponse('[{"name":"Hearter","switch":false,"icon":"heartIMG","status":"25%","mac":"00:00:01:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}},{"name":"111","switch":false,"icon":"heartIMG","status":"50%","mac":"00:00:02:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      // window.deviceListResponse('[{"name":"Humidity","switch":false,"icon":"heartIMG","status":"50%","mac":"00:00:02:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      window.temperatureAndHumidityDataResponse('00:00:02:00:00','{"temperature":{"currentValue":0,"hotAlarmValue":20,"hotAlarmSwitch":false,"codeAlarmValue":20,"codeAlarmSwitch":false},"humidity":{"currentValue":1000,"hotAlarmValue":50,"codeAlarmValue":10,"hotAlarmSwitch":true,"codeAlarmSwitch":true}}')
    }
    let obj = JSON.parse(getStore('HisTempData'))
    if (obj) {
      this.deviceList[0].currTemp = obj[0].currtemp
    }
  },
  methods: {
    ...mapActions(['showMessagesBox', 'setDevicesdata', 'setCurrentMac', 'loginStatus']),
    add () {
      if (this.loginStatus) {
        console.log('callThePhoneScanRequest')
        callThePhoneScanRequest(this.NAVIGATOR, this)
      } else {
        this.$router.push({ path: '/login' })
      }
    },
      listtiao(){
    this.$router.push({path:'/Home/device/temperature'});
    },
        alertChoose (num) {
      switch (num) {
        case 0:
          this.showMessagesBox({ 'type': 'null' })
          break
        case 1:
          this.showMessagesBox({ 'type': 'null' })
          this.hideDelet()
          break
        default:
          break
      }
    }
  },
  watch: {
    currentRouter (newnval) {
      console.log(newnval)
      if (newnval === 'device') {
        this.disJump = true
        temperatureAndHumidityDataRequest(this.NAVIGATOR, this)
        temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'device')
        let obj = JSON.parse(getStore('HisTempData'))
        if (obj) {
          this.deviceList[0].currTemp = obj[0].currtemp
        }
      }
      //  else if (newnval === 'temperature') {
      //   temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'temp')
      // }
    },
    deviceList (newnval) {
      if (newnval[0].currTemp === 100) {
        newnval[0].currTemp = 'N/A'
      }
    }
  }
}
</script>

<style scoped lang="less">
#me {
  .wh(100%, 100%);
  display: flex;
  flex-direction: column;
  font-size: 30px;
  color: #1b1b1b;
  .deviceList {
    margin: 0px 0px 0 25px;
    li {
      .wh(132%, 226px);
      display: flex;
      justify-content: space-between;
      margin-top: 25px;
      .side {
        .wh-l(240px, 100%);
        display: flex;
        flex-direction: row;
        font-size: 20px;
        color: #fcfcfc;
        // margin-left: 30px;
        .delate {
          width: 100%;
          background: #e93e3e;
          padding-top: 27px;
          font-size: 36px;
          line-height: 180px;
          box-shadow: 2px 2px 5px 2px #e93e3e;
        }
      }
    }
    li .left {
      display: flex;
      .wh(78%, 226px);
      background: #eeeeee;
      box-shadow: 2px 2px 5px 2px #d4d1d1;
      margin-right: 30px;
      img {
        .wh(94px, 94px);
        align-self: center;
        margin-left: 54px;
      }
      .deviceName {
        height: 94px;
        align-self: center;
        display: flex;
        flex-direction: column;
        margin-left: 47px;
        span:last-child {
          font-size: 27px;
          color: #17c569;
          margin-top: 28px;
        }
        .deviceOFF {
          color: #e94646;
        }
      }
      .currTemp {
        align-self: center;
        margin-left: 47px;
      }
    }
  }
  .adDevice {
    display: flex;
    .wh(100%, auto);
    justify-content: center;
    img {
      .wh(42px, 42px);
      margin-right: 10px;
    }
    position: absolute;
    bottom: 145px;
  }
  .common_alert {
    /deep/.determine_one {
      .wh-l(50%, 76px);
      margin: 0 52px 28px 78px;
      background: #aaaaaa;
      border-radius: 10px;
    }
    .ok {
      background: #e94646;
    }
    .alert_icon {
      margin-top: 41px;
    }
    .alert_describe {
      margin-top: 45px;
      color: #303030;
    }
  }
}
</style>



